.container {
  width: 60%;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 20px 0;
  display: flex;
}

.f1 {
  height: 100%;
  flex: 1;
}

.block {
  width: 60%;
  background: #0000004d;
  padding: 10px;
  box-shadow: 5px 5px 5px -5px #223c5099;
}

.mini-block {
  width: 75%;
  height: 300px;
  background: #00000080;
  padding: 10px;
  box-shadow: 5px 5px 5px -5px #223c5099;
}

.fullsize {
  min-height: auto;
  flex-direction: column;
  align-items: stretch;
  display: flex;
}

.rectangle {
  counter-reset: li;
  text-shadow: 0 1px #ffffff80;
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
}

.rectangle a {
  color: #fff;
  background: #293747;
  margin: .5em 0 .5em 2.5em;
  padding: .4em .4em .4em .8em;
  text-decoration: none;
  transition: all .3s ease-out;
  display: block;
  position: relative;
}

.rectangle a:hover {
  background: #6588b0;
}

.rectangle a:before {
  content: counter(li);
  counter-increment: li;
  height: 2em;
  width: 2em;
  text-align: center;
  background: #90c3fc;
  margin-top: -1em;
  font-weight: bold;
  line-height: 2em;
  position: absolute;
  top: 50%;
  left: -2.5em;
}

.rectangle a:after {
  content: "";
  border: .5em solid #0000;
  margin-top: -.5em;
  transition: all .3s ease-out;
  position: absolute;
  top: 50%;
  left: -1em;
}

.rectangle a:hover:after {
  border-left-color: #90c3fc;
  left: -.5em;
}

.avatar {
  object-fit: cover;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  box-shadow: 5px 5px 5px -5px #223c5099;
}

.avatar-container {
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

